.container {
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-content: center;
    align-items: center;
}

.container a {
    text-decoration: none;
    color: black;
}

.container a:hover {
    color: white;
}

.list {
    list-style: none;
    padding: 0;
    font-size: 1rem;
}

.gitfont { 
    font-size: 1rem;
}

@media only screen and (max-width: 768px) {
    .container {
        width: 100%;
        text-align: center;
    }
    .gitfont { 
        font-size: .5rem;
    }
}

@media only screen and (max-width: 320px) {
  h1 {
      font-size: 12px;
  }
  a {
      font-size: 8px;
  }
  ul {
      padding: 0 !important;
  }
    .gitfont { 
        font-size: .5rem;
    }
}

@media only screen and (max-width: 375px) {
    h1 {
        font-size: 2vw;
    }
    a {
        font-size: 2vw;
    }
    ul {
        padding: 0 !important;
    }
    .gitfont { 
        font-size: .5rem;
    }
  }